@import url(https://fonts.googleapis.com/css2?family=Trykker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --green: #85b3a0;
  --paleGreen: #85b3a071;
}
body {
  background-image: url(/to-do-list/static/media/cacti-pale.e4f4016c.jpeg);

  background-repeat: no-repeat;
  background-size: cover;
}
.App {
  text-align: center;
  font-family: "Trykker", serif;
}

li {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  padding: 1rem;
  justify-content: center;
}
li button {
  margin-right: 1rem;
}

.strike {
  text-decoration: line-through;
  color: red;
  border: red solid 0.5px;
}
.strike-date {
  text-decoration: line-through;
  color: red;
}

button {
  width: 35%;
  padding: 0.5rem;
  background-color: rgba(226, 240, 234, 0.986);
  font-family: "Trykker", serif;
  border-color: rgb(19, 53, 50);
  margin: 1rem;
  border-radius: 25px;
}
button:hover {
  box-shadow: 0 0 5px 4px rgba(57, 58, 57, 0.623);

  transition: 0.5s ease;
}
#list-item {
  padding: 1rem;
}
#due-date {
  padding: 0.5rem;
}
.input-form {
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-bottom: 1rem;
  margin-top: 0.5rem;
  width: 50%;
  box-shadow: 2px 2px 5px rgb(66, 66, 66);
  border-radius: 10px;
}

label {
  margin-bottom: 2rem;
  font-size: 1.2rem;
}
.App-header {
  background-color: #85b3a0;
  /* #1f331f;*/
  /*#9ad6bd;*/
  background-position-y: 18%;
  background-size: cover;
  min-height: 20px;
  padding-bottom: 1.5rem;
  padding-top: 1.5rem;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(13px + 2vmin);
  color: white;
}

@media (min-width: 1000px) {
  button {
    width: 15%;
    padding: 0.5rem;
    font-size: 1.5rem;
    margin: 2rem;
  }
  li {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    padding: 1rem;
    justify-content: center;
    margin: auto;
  }
  li button {
    margin-right: 2rem;
  }

  .strike {
    text-decoration: line-through;
    color: red;
    border: red solid 2px;
  }

  .input-form {
    margin-right: 2rem;
    margin-left: 1rem;
    display: flex;
    flex-direction: row;
    margin: auto;
    width: 20%;
    padding: 1rem;
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: 1.5rem;
  }
  .App-header {
    background-position-y: 15%;
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
  .due-date-text {
    font-size: 1.5rem;
  }
  .strike-date {
    font-size: 1.5rem;
  }
}

